import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    slideStatus: true
  },
  mutations: {
    changeSlideStatus (state, bool) {
      state.slideStatus = bool
    }
  },
  actions: {
    CHANGESLIDESTATUS ({ commit }, bool) {
      commit('changeSlideStatus', bool)
    }
  },
  modules: {
  }
})
